import axios from 'axios';
import vueRoot from '../main'
let base = '';
var instance = axios.create({
  headers: {'Content-Type': 'application/json'}
});

//初始化拦截器
instance.interceptors.response.use(function (response,success,error) {
  if (response.data.operateResult != "SUCCESS") {
	vueRoot.$Message.error(response.data.msg);
  } else {
	vueRoot.$Message.success(response.data.msg);
  }
  return response.data;
}, function (error) {
  if(error.response.status != 200){
	vueRoot.$Message.error("请求失败")
  }else{
	vueRoot.$Message.error(response.data.msg);
  }
});
//用户模块
export const userQuery = params => { return instance.post(`${base}/user/query`, params).then(res => res.data); };
export const addUser = params => { return instance.post(`${base}/user/add`, params).then(res => res.data); };
export const editUser = params => { return instance.post(`${base}/user/update`, params).then(res => res.data); };
//菜单模块
export const addMenu = params => { return instance.post(`${base}/menu/add`, params).then(res => res.data); };
export const deleteMenu = params => { return instance.post(`${base}/menu/delete`, params).then(res => res.data); };
export const updateMenu = params => { return instance.post(`${base}/menu/update`, params).then(res => res.data); };
export const loadMenuTree = params => { return instance.post(`${base}/menu/getChildes`, params).then(res => res.data); };
//数据库字典模块
export  function findDict(path) {
  return[
	{value: 1, label: '有效'},
	{value: 0, label: '无效'}
  ];
}


