<style scoped>

</style>

<template>
  <div>
    <!-- 主体风格 -->
    <Form :model="userForm" :rules="userFormRule"  :label-width="90">
      <input type="hidden" v-model="userForm.userId" >
      <Form-item prop="userCode"   label="用户登陆代码">
        <Input type="text" v-model="userForm.userCode" placeholder="请用户登陆代码"/>
      </Form-item>
      <Form-item prop="userPwd" label="用户密码">
        <Input type="password" v-model="userForm.userPwd" placeholder="请用户密码"/>
      </Form-item>
      <Form-item prop="userState"  label="用户状态" placeholder="请用户状态">
        <Select v-model="userForm.userState" >
          <Option v-for="item in userStateList" :value="item.value" :key="item">{{ item.label }}</Option>
        </Select>
      </Form-item>
    </Form>
  </div>
</template>

<script>
  import {findDict} from '../../api/api'
  export default {
    name: 'UserForm',
    data () {
      return {
        userStateList:findDict('dada'),
        userForm: {userId:'',userCode: '', userPwd: '', userState:''},
        userFormRule: {
          userCode: [
            {required: false, message: '请填写用户名', trigger: 'blur'}
          ],
          userPwd: [
            {required: false, message: '请填写密码', trigger: 'blur'},
            {type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur'}
          ],
          userState:[
            {required: false, message: '请填写密码', trigger: 'blur'},
          ]
        }
      }
    },
    methods: {

    }
  }
</script>



