<style scoped>
  @import '../../styles/css/common.css';
</style>

<template>
  <div>
    <!-- 主体风格 -->
    <Row style="margin-bottom: 20px">
      <Col span="7" >
        <DepartTree></DepartTree>
      </Col>
      <Col span="16" offset="1">
      <Card :bordered="true" :padding=0 style="margin-bottom: 20px">
        <div slot="title">
          <Row class="panel">
            <Form ref="queryForm" label-position="left" :label-width="80" :model="formInline"
                  :rules="ruleInline" inline>
              <Form-item prop="user" label="用户名称">
                <Input size="small" type="text" v-model="formInline.userCode" placeholder="用户名称"/>
              </Form-item>
              <Form-item prop="userState"  label="用户状态">
                <Select v-model="formInline.userState"   style="width: 100px" placeholder="请选择用户状态" >
                  <Option v-for="item in userStateList" :value="item.value" :key="item">{{ item.label }}</Option>
                </Select>
              </Form-item>
              <Form-item :label-width="10">
                <Button :loading="loadSate.query" icon="ios-search" type="ghost"
                        @click="queryUserList('queryForm')">查询用户信息
                </Button>
              </Form-item>
            </Form>
          </Row>

          <Row>
            <Button type="ghost" icon="android-add-circle" @click="addUser">新增</Button>
            <Button type="ghost" icon="edit" @click="editUser">修改</Button>
            <Dropdown>
              <Button type="ghost" icon="more" style="width: 100px">更多操作
                <Icon type="arrow-down-b"></Icon>
              </Button>
              <Dropdown-menu slot="list">
                <Dropdown-item>禁用用户</Dropdown-item>
                <Dropdown-item>启用用户</Dropdown-item>
                <Dropdown-item>删除用户</Dropdown-item>
              </Dropdown-menu>
            </Dropdown>
          </Row>
        </div>
        <Row v-if="page !== undefined">
          <Table :context="self" size="small" @on-selection-change="tableSelectionChange" :data="table.gridData"
                 :columns="table.columns" stripe></Table>
        </Row>
        <Row style="margin: 15px 0px">
          <Page style="float: right" size="small" :page-size="page.pageSize"
                :current="page.pageNo" @on-page-size-change="changePageSize"
                :total="page.total" show-sizer @on-change="changePage" show-total></Page>
        </Row>

      </Card>
      </Col>
    </Row>
    <Modal v-model="model.addFrom" title="用户信息修改" @on-ok="submit" @on-cancel="cancel">
      <UserForm ref="userForm"></UserForm>
    </Modal>
  </div>
</template>

<script>
  import UserForm from '../../components/user/user-add-form.vue'
  import  moment from  'moment';
  import copyProps from 'copy-props'
  import DepartTree from '../../components/depart/tree.vue'
  import {userQuery,addUser,editUser} from '../../api/api';


  function removeValue(object) {
    Object.keys(object).filter(function (key) {
      object[key] ='';
    })
  }

  function submitUser(contenxt) {
    let that = contenxt;
    that.$refs["queryForm"].validate(function (valid) {
      if (valid) {
        let userForm = that.$refs["userForm"].userForm;
        if(userForm.userId>0){
          editUser(userForm).then(function(data) {
            if (data.operateResult != "SUCCESS") {
              that.$Message.error(data.msg);
            }else {
              that.$Message.success(data.msg);
              that.tableData = that.queryUserData();
            }
          })
        }else{
          addUser(Object.assign({departmentId:1},userForm)).then(function (data) {
            console.info(data);
            if (data.operateResult != "SUCCESS") {
              that.$Message.error(data.msg);
              that.loadSate.query = false;
            }else {
              that.$Message.success(data.msg);
              that.tableData = that.queryUserData();
            }
          })
        }
      } else {
        that.$Message.error('表单验证失败!');
      }
    })
  }

  function dateTime(row, column, index) {
    let value = row[column.key];
    if (typeof  value == "number") {
      return moment(value).format('YYYY-MM-DD HH:mm:ss');
    } else {
      return value;
    }
  }

  function stateFormate(row, column, index) {
    let value = row[column.key];
    let result = "未知状态";
    if (value == 1) {
      result = "有效"
    } else if (value == 0) {
      result = "无效"
    }
    return result;
  }

  export default {
    data () {
      return {
        userStateList:[
          {value: 1, label: '有效'},
          {value: 0, label: '无效'}
        ],
        page: {
          pageSize: 20,
          pageNum: 1,
          total: 0
        },
        table: {
          selectRowData: new Array(),
          columns: [
            {type: 'selection', width: 60, align: 'center'},
            {title: '用户登录帐号', key: 'userCode'},
            {title: '状态', key: 'userState', render: stateFormate},
            {title: '创建时间', key: 'userCTime', render: dateTime},
            {title: '修改时间', key: 'userUTime', render: dateTime}
          ],
          gridData: new Array(),
        },
        model: {addFrom: false},
        loadSate: {query: false, add: false, edit: false},
        formInline: {userCode: '',userState:'1'},
        ruleInline: {
          user: [
            {required: false, message: '请填写用户名', trigger: 'blur'}
          ],
          password: [
            {required: false, message: '请填写密码', trigger: 'blur'},
            {type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur'}
          ]
        },
        self: this
      }
    },
    methods: {
      //数据选择
      tableSelectionChange(selection){
        this.table.selectRowData = selection;
      },
      submit(){
        submitUser(this);
      },
      cancel(){
      },
      addUser(){
        let that = this;
        that.model.addFrom = true;
        removeValue(that.$refs["userForm"].userForm);
      },
      editUser(){
        let that = this;
        if (that.table.selectRowData.length != 1) {
          that.$Message.success('请选择一条数据!');
          return;
        }
        that.model.addFrom = true;
        //拷贝指定的数据
        copyProps(that.table.selectRowData[0],that.$refs.userForm.userForm,["userCode","userId","userPwd","userState"])
      },
      queryUserList(name) {
        let that = this;
        try {
          that.loadSate.query = true;
          this.$refs[name].validate(function (valid) {
            if (valid) {
              that.$Message.success('提交成功!');
              that.tableData = that.queryUserData();
            } else {
              that.$Message.error('表单验证失败!');
            }
          })
        } catch (e) {

        } finally {
          that.loadSate.query = false;
        }

      },
      queryUserData() {
        let self = this;
        var queryData = Object.assign(this.formInline, this.page);
        userQuery(queryData).then(function (data) {
          self.table["gridData"] = data.list;
          self.page.total = data.total;
        })
      },
      changePage (pageNo) {
        this.page.pageNum = pageNo;
        this.queryUserData();
      },
      changePageSize (pageSize){
        this.page.pageSize = pageSize;
        this.queryUserData();
      }
    },
    components: {
      UserForm,DepartTree
    },
    created: function () {
      this.table.gridData = this.queryUserData();
    }
  }
</script>



