<style scoped>
  .list-container {
	padding: 10px;
	border: solid 1px #d7dde4;
  }

  .list-container .title-line {
	margin: 10px;
  }

  .list-container .line {
	margin: 10px;
	border-bottom: solid 1px #d7dde4;
  }

  .list-container .title {
	color: #464c5b;
	text-align: right;
	vertical-align: middle;
	float: left;
	font-size: 12px;
	color: #657180;
	line-height: 1;
	padding: 10px 12px 10px 0;
	box-sizing: border-box;
  }

  .list-container .item {
	color: #657180;
  }
</style>
<template>
  <div>
	<el-row>
	  <Card :bordered="true" :padding=0 class="m5">
		<div slot="title">
		  <el-row class="m5">
			<el-col :span="12">
			  <Input v-model="filterText" placeholder="根据名字搜索树..." style="width: 300px"></Input>
			</el-col>
		  </el-row>
		  <el-row class="m5">
			<Button type="ghost" icon="android-add-circle" @click="addMenu">新增</Button>
			<Button type="ghost" icon="edit">修改</Button>
		  </el-row>
		</div>
		<el-row class="m5">
		  <strong class="title">菜单树</strong>
		  <el-tree ref="menuTree" class="filter-tree m5" :highlight-current="true" :data="menuData"
				   :default-expanded-keys="[1]" :auto-expand-parent="true"
				   node-key="menuId" :props="defaultProps" show-checkbox
				   :render-content="renderContent" :check-strictly="true" :filter-node-method="filterNode"></el-tree>
		</el-row>
	  </Card>
	</el-row>
	<Modal
	  v-model="modal1"
	  title="增加菜单"
	  @on-ok="saveMenu"
	  @on-cancel="cancelSaveMenu">
	  <Form :model="formItem" :label-width="80">
		<Form-item label="输入框">
		  <Input v-model="formItem.input" placeholder="请输入"></Input>
		</Form-item>
		<Form-item label="选择器">
		  <Select v-model="formItem.select" placeholder="请选择">
			<Option value="beijing">北京市</Option>
			<Option value="shanghai">上海市</Option>
			<Option value="shenzhen">深圳市</Option>
		  </Select>
		</Form-item>
		<Form-item>
		  <Button type="primary">提交</Button>
		  <Button type="ghost" style="margin-left: 8px">取消</Button>
		</Form-item>
	  </Form>
	</Modal>

  </div>
</template>

<script>
  import {Icon} from 'iview'
  import {loadMenuTree} from '../../api/api'
  var id = 10000;
  export default {
	watch: {
	  filterText(val) {
		this.$refs.menuTree.filter(val);
	  },
	  expandedKeys(val){
	  }
	},
	methods: {
	  filterNode(value, data) {
		if (!value) return true;
		return data.menuName.indexOf(value) !== -1;
	  },
	  append(node, store, data,$event) {
		$event.stopPropagation();
		node.expanded = true;
		store.append({menuId: id++, menuName: '传奇世界', children: []}, data);
	  },
	  remove(node, store, data) {
		$event.stopPropagation();
		store.remove(data);
	  },
	  renderContent(h, {node, data, store}) {
		return (
		  <span>
			<span>
			  <Icon type={data.menuIcon} style="padding:0px 5px" />
			  <span>{node.label}</span>
			</span>
		  </span>);
	  }
	},
	data() {
	  return {
		filterText: '',
		expandedKeys: [1],
		menuData: [],
		oneSelectNode: null,
		defaultProps: {
		  children: 'children',
		  label: 'menuName'
		}
	  };
	},
	component:{
	  Icon
	},
	mounted: function () {
	  let self = this;
	  loadMenuTree(1).then(function (data) {
		self.menuData = data;
	  })
	}
  };
</script>



