import Login from "./views/Login.vue";
import NotFound from "./views/404.vue";
import Home from "./views/Home.vue";
import UserGrid from "./views/user/userGrid.vue";
import MenuTree from "./views/menu/menuGrid.vue";
let routes = [
  {
	path: '/login',
	component: Login,
	name: '',
	hidden: true
  },
  {
	path: '/404',
	component: NotFound,
	name: '',
	hidden: true
  },
  {
	path: '/',
	component: Home,
	name: '组织管理',
	meta: {iconCls: 'el-icon-message'},
	children: [
	  {path: '/table', component: UserGrid, name: '用户管理'},
	  {path: '/cc', component: UserGrid, name: '部门管理'}
	]
  },
  {
	path: '/',
	component: Home,
	name: '授权管理',
	meta: {iconCls: 'el-icon-message'},
	children: [
	  {path: '/menuTree', component: MenuTree, name: '菜单管理'}
	]
  }
];
export default routes;


